import React, { useEffect, useState } from "react";
import "./styles.css";
import CardSkeleton from "./components/cardSkeleton";
import CardWeather from "./components/cardWeather";
import SearchBlock from "./components/searchBlock";
// import AutoSearch from "./components/autoSearch";

export default function App() {
  const [searchValue, setSearchValue] = useState("Delhi");
  const [tempInfo, setTempInfo] = useState({});
  const [loading, setLoading] = useState(true);

  const getWeatherData = async () => {
    try {
      let url = `https://api.weatherapi.com/v1/current.json?key=bbc3b8ff550c491e99963810211509&q=${searchValue}&aqi=yes`;
      let response = await fetch(url);
      let data = await response.json();
      setLoading(false);
      const { temp_c, temp_f, humidity, pressure_in, wind_mph } = data.current;
      const { text: status, icon } = data.current.condition;
      const {
        name: city,
        country,
        region,
        localtime_epoch: local_time,
        localtime
      } = data.location;
      console.log(data);

      console.log(temp_c, temp_f, status, pressure_in, localtime);
      const tempData = {
        temp_c,
        temp_f,
        status,
        icon,
        humidity,
        city,
        country,
        region,
        pressure_in,
        wind_mph,
        local_time,
        localtime
      };
      setTempInfo(tempData);
    } catch (error) {
      console.log(error);
    }
  };

  // const {
  //   temp_c,
  //   temp_f,
  //   status,
  //   icon,
  //   humidity,
  //   city,
  //   country,
  //   region,
  //   pressure_in,
  //   wind_mph,
  //   local_time,
  //   localtime
  // } = tempInfo;

  // let second = local_time;
  // let date = new Date(second * 1000);
  // let time = `${date.getHours()}:${date.getMinutes()}`;

  const handleChange = (e) => {
    setSearchValue(e.target.value);
    // console.log(e.target.value);
  };

  useEffect(() => {
    setTimeout(() => {
      getWeatherData();
    }, 1000);
  }, []);

  // if (searchValue.length === 0) {
  //   return "Data Loading...";
  // }

  return (
    <div className="main-card" width="200px">
      {loading ? (
        <CardSkeleton />
      ) : (
        <>
          <SearchBlock
            searchValue={searchValue}
            getWeatherData={getWeatherData}
            handleChange={loading ? <CardSkeleton /> : handleChange}
          />

          <CardWeather tempInfo={tempInfo} />
        </>
      )}
    </div>
  );
}
